@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600&display=swap');
@font-face {
  font-family: "ZagW90-Bold";
  src: url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.eot");
  /* IE9*/
  src: url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
  url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.woff2") format("woff2"), /* chrome firefox */
  url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.woff") format("woff"), /* chrome firefox */
  url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.ttf") format("truetype"), /* chrome firefox opera Safari, Android, iOS 4.2+*/
  url("https://db.onlinewebfonts.com/t/032f34bc3ead02aba003fa26a401505c.svg#ZagW90-Bold") format("svg");
  /* iOS 4.1- */
}

* {
  /*reset comportamento padr√£o do HTML*/
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

/*coloca 100% largura e altura da tela*/

#root, body, html {
  height: 100%;
}

body {
  background: #13101b;
  background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('./images/treta-big.png');
  background-size: auto auto;
  background-position: 50%;
  background-repeat: no-repeat;
  /*deixa a font mais legivel em brower que utiliza webkit*/
  -webkit-font-smoothing: antialiased;
}

/* body, input {
  /* font-family: ZagW90-Bold, Zag, sans-serif;
} */

button {
  cursor: pointer;
}

button, div {
  font-family: 'Open Sans', sans-serif;
  text-rendering: optimizeLegibility;
}

p {
  font-family: ZagW90-Bold, sans-serif;
  padding-left: 1vmin;
  /*margin-inline-start: 1vmin;/*/
  /* text-shadow: 2px 3px 8px rgba(0, 0, 0, .2); /*/
  text-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
}