main {
  max-width: 960px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  row-gap: 1vmin;
  text-align: center;
  color: white;
  user-select: none;
}

.corporation {
  text-shadow: 2px 3px 8px rgba(0, 0, 0, .8);
  font-size: 2.4vw;
  font-weight: bold;
}

.rodada {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  column-gap: calc(5px + 2vmin);
  width: 80%;
  padding: 0 0 0 1%;
}

.rodada-nova {
  width: 60%;
  padding: 2.3vw 0 1.5vw 0;
  border-top: 35px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.rodada-escolha {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  column-gap: calc(5px + 2vmin);
  transition: all 0.25s linear;
  flex-grow: 1;
}

.rodada-button, .rodada-button-new {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  cursor: pointer;
}

.rodada-button p {
  font-size: 11vw;
  text-shadow: none;
}

.rodada-button-new p {
  font-family: "Agency FB", sans-serif;
  font-size: 9vw;
}

.rodada-button-new img {
  width: 83%;
}

.rodada-num {
  background-image: url(../images/Rodada.png);
  background-repeat: round;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33%;
  font-size: 14vw;
  padding: 3.7vw 0 4vw 0;
}

.nivel {
  background-image: url(../images/Nivel.png);
}

.energia {
  background-image: url(../images/Energia.png);
}

.fortaleza {
  background-image: url(../images/Fortaleza.png);
}

.nivel, .energia, .fortaleza {
  background-repeat: round;
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding-top: 3%;
  /* max-height: 224px;/*/
  /*remover*/
  /*height: 224px; /*/
  /* border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf; */
}

.fortaleza {
  padding-top: 3.2%;
  padding-bottom: 1%;
  justify-content: space-around;
}

.reserva-movel {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr;
  width: 80%;
  column-gap: 20px;
  font-size: 26vw;
  padding-bottom: .5%;
}

.reserva-movel#fortaleza {
  height: 26vw;
}

.bt-movel {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
  padding-top: 20%;
  cursor: pointer;
}

.bt-movel img {
  width: 12vmin;
}

.reserva-conquista {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  width: 51%;
  font-size: 11vw;
}

.reserva-conquista button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.reserva-conquista p {
  background-color: rgba(0, 0, 0, .7);
}

.reserva-conquista img {
  width: 98%;
}

.compra {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2% 0 3% 0;
}

.compra-aviso {
  width: 88%;
  text-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  font-weight: bold;
  font-style: italic;
  font-size: 4vmin;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-items: center;
  align-items: center;
  animation: fadein 0.5s linear;
}

.compra-divisa {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3.2% 0 3.1% 0;
  font-size: 4vmin;
}

.linha-aviso {
  width: 85%;
  /* border-top: 2px solid #fff;/ */
  height: 0.33vw;
  background-color: #fff;
  -webkit-box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  -moz-box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
}

.compra-card {
  width: 75%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 8vw;
}

.compra-card button {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
}

.compra-marco {
  display: flex;
  flex-direction: row;
}

.marco-checked {
  display: flex;
  flex-direction: column;
  width: 6vw;
}

.marco-checked img {
  width: 6vw;
}

/* anima ao ativar */

.compra-ativa {
  will-change: animate;
  animation: dropshadow 1.5s ease-in;
  animation-iteration-count: infinite;
}

/* anima cada modifica√ß√£o de elemento*/

.entrada {
  animation: fadein 0.5s linear;
}

.destaque {
  animation: dropshadow 0.3s ease-in-out;
}

@media screen and (min-width: 500px) {
  main {
    row-gap: 4.62px;
  }
  .corporation {
    font-size: 12px;
  }
  .rodada {
    width: 400px;
    padding: 0 0 0 6px;
  }
  /* .rodada-escolha p {
    font-size: 70px;
  } */
  .rodada-nova {
    padding: 11px 0 7px 0;
  }
  .rodada-num {
    width: 131px;
    height: 114px;
    font-size: 70px;
    padding: 0 0 0 0;
  }
  .rodada-button p {
    font-size: 55px;
  }
  .rodada-button-new p {
    font-size: 45px;
  }
  .nivel, .energia, .fortaleza {
    width: 400px;
    height: 224px;
    padding-top: 15px;
  }
  .fortaleza {
    height: 242px;
    padding-top: 16px;
    padding-bottom: 5px;
  }
  .reserva-movel {
    padding-bottom: 0;
    font-size: 130px;
  }
  .reserva-movel#fortaleza {
    height: 130px;
  }
  .reserva-conquista {
    font-size: 55px;
  }
  .bt-movel img {
    width: unset;
  }
  .compra {
    padding: 8px 0 12px 0;
  }
  .compra-aviso {
    font-size: 20px;
  }
  .compra-divisa {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12.9px 0 12.9px 0;
    width: 100%;
  }
  .linha-aviso {
    height: 1.75px;
  }
  .compra-card {
    font-size: 40px;
  }
  .marco-checked {
    width: 29.97px;
  }
  .marco-checked img {
    width: 29.97px;
  }
}