@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600&display=swap);
.toolbar {
  background-image: linear-gradient(to top, rgb(0 0 0 / 0%), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.6));
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2% 0 1% 0;
}

.content-toolbar {
  width: 100%;
  height: 7vmin;
  background-image: url(/sevengalaxies-app/static/media/Logo_wide.35138d32.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  padding-right: 1.5vw;
}

.menu {
  height: 100%;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 500px) {
  .toolbar {
    padding: 10.0156px 0 5px 0;
  }
  .content-toolbar {
    width: 500px;
    height: 35px;
    padding-right: 7.5px;
  }
}
.modal {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  background-color: rgba(0, 0, 0, .85);
  border: solid 1px #4a4a4a;
  width: calc(200px + 20vmin);
  max-height: 660px;
  /* background-color: #36393f; */
  box-shadow: 0 0 0 1px rgba(32, 34, 37, .6), 0 2px 10px 0 rgba(0, 0, 0, .2);
  border-radius: 2px;
  margin: 0 auto;
  pointer-events: all;
  position: relative;
  display: grid;
  grid-template-rows: 2fr 1fr;
  justify-content: center;
  align-items: center;
}

.message {
  padding: calc(5px + 1vmin);
  text-align: start;
  margin-bottom: 10px;
  font-size: calc(8px + 1vmin);
}

.options {
  margin: 0 calc(5px + 1vmin) 0 calc(5px + 1vmin);
  padding: calc(5px + 1vmin) 0;
  border-radius: 0 0 5px 5px;
  border-top: 2px solid #414141;
  display: flex;
  justify-content: flex-end;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.options .action {
  -webkit-appearance: button;
     -moz-appearance: button;
          appearance: button;
  width: calc(60px + 2vmin);
  min-height: 30px;
  min-width: 90px;
  font-size: calc(8px + 1vmin);
  font-weight: bold;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: pointer;
  color: #fff;
}

.options .cancel {
  font-size: calc(8px + 1vmin);
  font-weight: bold;
  color: #fff;
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
}
